/************************ОБВЁРТКА ЛЕВЫХ САЙТБАРОВ ПРОФИЛЯ !!!*********************/
.LshadowSidebar {
  border-top: 1px solid #000; 
  background-color: rgba(40, 40, 40, 0.9);
  -moz-box-shadow: inset 0 30px 30px -30px #7F7F7F, inset 0 -30px 30px -30px #7F7F7F;
  -webkit-box-shadow: inset 0 30px 30px -30px #7F7F7F, inset 0 -30px 30px -30px #7F7F7F;
  box-shadow: inset 0 30px 30px -30px #606060, inset 0 -30px 30px -30px #606060;
  // box-shadow: 0 0 10px 1px black;
  border-bottom: 1px solid #000;
  border-right: 1px solid #000;
}

.L_wrapper_Sidebar {
  padding: 25px;
  width: 250px;
  height: 100vh;
  border-top: 1px solid #4D4D4D;
  border-left: 1px solid #000;
  border-bottom: 2px solid #000;
  background: url(../../assets/img/sidebar/L_Top_Sidebar.png) no-repeat top left,
                url(../../assets/img/sidebar/R_Top_Sidebar.png) no-repeat top right,
                url(../../assets/img/sidebar/Top_center_Sidebar.png) repeat-x top center,
                url(../../assets/img/sidebar/L_Bottom_Sidebar.png) no-repeat bottom left,
                url(../../assets/img/sidebar/R_Bottom_Sidebar.png) no-repeat bottom right,
                url(../../assets/img/sidebar/bottom_center_Sidebar.png) repeat-x bottom center,				
                url(../../assets/img/sidebar/L_center_Sidebar.png) repeat-y left center,
                url(../../assets/img/sidebar/R_center_Sidebar.png) repeat-y right center,
                #252525;
  box-shadow: 0px 0px 5px 1px black;

}
